import React, {useState} from 'react'
import logo from '../logo-1.png'
import axios from 'axios'
import { motion } from 'framer-motion';
export default function Home() {
    const [name, setName] = useState();
    const [mes, setMes] = useState();
    return (
        <div className="App">
            <div className='loading_line_top'><div className='loading_line_main'></div></div>
            <header>
                <div>
                    <img src={logo} className="header-logo" />
                </div>
                <div style={{ width: '95%', borderTop: '2px solid black' }} >
                    <ul>
                        <li className="href"><a href='/'></a></li>
                        <li className="href"><a href='/contacts'></a></li>
                        <li className="href active"><a href='/feedback'> </a></li>
                    </ul>
                </div>
            </header>
            <motion.div className="content w_shadow"
                style={{ padding: 15, width: 600, maxWidth: '90%', margin: '0 auto'}}
                initial={{ opacity: 0, x: -10 }}
                animate={{ opacity: 1, x: 0 }}>
                <div style={{textAlign: 'center'}}>
                    <h2> </h2>
                </div>
                <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'space-around', textAlign: 'center'}}>
                    <input value={name} onChange={e => setName(e.target.value)} placeholder=' '/>
                    <textarea value={mes} onChange={e => setMes(e.target.value)} placeholder=' ' rows='10' cols='50'></textarea>
                    <button style={{width: 250}} onClick={() => {
                        axios.get(`https://odji-coffee.ru/server/api.php?mes=${mes}&name=${name}`)
                        .then(resp => {
                            console.log(resp)
                            window.location.href = '/'
                        })
                    }}></button>
                </div>
            </motion.div>
        </div>
    )
}